import logo from "./logo.svg";
import "./App.css";
import { useEffect, useState } from "react";
import Select from "react-select";
import axios from "axios";
import {
  Card,
  Progress,
  CardBody,
  CardHeader,
  Col,
  Container,
  ListGroup,
  ListGroupItem,
  Row,
} from "reactstrap";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import "react-tabs/style/react-tabs.css";

axios.defaults.baseURL = document
  .querySelector("meta[name=apiPath]")
  .getAttribute("content");

function App() {
  const [infos, setInfos] = useState([]);
  const [percentage, setPercentage] = useState(0);
  const [indexTab,setIndex]=useState(0)
  const [clicked,setClicked]=useState(false)
  const [hidden,setHidden]=useState(false)
  const [hidden2,setHidden2]=useState(false)
  const options = [
    { value: "classe", label: "Classe" },
    { value: "administration", label: "Administration" },
    { value: "evaluations", label: "Evaluations et notes" },
    { value: "encaissements", label: "Encaissements" },
    { value: "competences", label: "Competences" },
    { value: "inscriptions", label: "Inscriptions" },
    { value: "users", label: "Users" },
    { value: "enseignants", label: "Enseignants" },
    
  ];
  useEffect(() => {
    // axios.get("http://localhost:5000/api/inscriptions").then((res) => {
    //   setInfos(res.data)
    //   console.log(res.data)
    // }
    //   )
  }, []);

  useEffect(() => {
    setTimeout(() => {
      if (percentage < 100) {
        setPercentage(percentage + 25);
      } else {
        return;
      }
    }, 0);
  });

  const selectHandler = () => {
    setPercentage(0);
    setClicked(false)
    console.log('index is: ',indexTab)
    // const selectedValue = e.value;
    let tmpData=[]
    axios.get("" + options[indexTab].value).then((res) => {
      tmpData=res.data
      tmpData.map((d)=>{
       d.hidden=false
      })
      setInfos(tmpData);

    });
    


  };

  const toggleItem = (index) => {
    let items = infos;
    items[index].hidden = !items[index].hidden; // mutating
    setInfos(items); // new object triggers re-render
    console.log(infos)
  };


  return (
    <div className="App">
      <div className="container">
        <div className="row my-5">
          <div className="col-12 text-center">
           <h1 className="fw-light"><img src="https://cdn-icons-png.flaticon.com/512/1008/1008786.png" width={50} alt="" />  Boti Data</h1>
          </div>
        </div>

      <Tabs defaultIndex={0} onSelect={(i) =>{setIndex(i);setClicked(true)}}>
        <TabList>
          <Tab>Classe</Tab>
          <Tab>Administration</Tab>
          <Tab>Evaluations</Tab>
          <Tab>Encaissement</Tab>
          <Tab>Competences</Tab>
          <Tab>Inscriptions</Tab>
          <Tab>Users</Tab>
          <Tab>Enseignants</Tab>
        </TabList>
        {options.map((op,index)=>{
          return <TabPanel key={index}>
            <div className="row d-flex flex-row-reverse">
                    <div className="col-3 text-end">
                    <button className="btn btn-primary" onClick={selectHandler}>Check Up</button>
                    </div>
             </div>
             {clicked==true && infos.length!==0 && infos[0].actualTable!==''?"":infos.map((info,index) => {
            return (
              <div className="col-12 my-3">
                <Card className="p-3">
                  <div className="row">
                    <div className="col-10 pt-4">
                    {options[indexTab].label==="Users" && info.key=="2"?
                    <p style={{fontSize:"18px"}}>{info.message}</p>
                    // La table <span style={{fontWeight:"bold"}}>{info.actualTable}</span> contient <span style={{fontWeight:"bold"}}>{info.nb_missing_in}</span> valeurs rpts pour la colonne <span style={{fontWeight:"bold"}}>{info.attribute}</span>
                    :options[indexTab].label==="Encaissements" && info.key=="3"?<p>Les inscriptions de la promotion actuelle qui n'ont aucun service obligatoire affect.</p>:options[indexTab].label==="Encaissements" && info.key=="4"?<p>Les encaissements avec numro de reu rpt.</p>
                    :options[indexTab].label==="Enseignants" && info.key=="1"?<p>Les parents qui sont en mme temps des enseignants.</p>
                    :options[indexTab].label==="Inscriptions" && info.key=="2"?<p>{info.message}</p>
                    :<p style={{fontSize:"18px"}}>La table <span style={{fontWeight:"bold"}}>{info.actualTable}</span> contient <span style={{fontWeight:"bold"}}>{info.nb_missing_in}</span> valeurs manqus dans la table <span style={{fontWeight:"bold"}}>{info.alias}</span> pour la colonne <span style={{fontWeight:"bold"}}>{info.attribute}</span></p>}
                    <div className="pt-5">
                    <h6 className="text-end text-muted">{percentage} %</h6>
                      <Progress color={info.nb_missing_in==0?"success":"danger"} value={percentage} />
                      <p style={{textAlign:"end"}}>
                      <button className="btn mt-5" onClick={()=>{setHidden(!hidden)}}><h4><i class="bi bi-eye"></i></h4></button>
                      </p>
                      {hidden==true?<h5 key={index} className="mt-4">{info.query}</h5>:""}
                      {percentage == 100 ? (
                        info.nb_missing_in != 0 ? (
                          <p className="bg-light text-black p-3 rounded" style={{color:"white"}}>
                            {/* <span >
                              <i
                                style={{ fontSize: "35px" }}
                                class="bi bi-x-lg"
                              ></i>
                            </span> */}
                            <p>
                              <p style={{textAlign:"end"}}>
                              <button className="btn mt-5" onClick={()=>{setHidden2(!hidden2)}}><h4><i class="bi bi-eye"></i></h4></button>
                              </p>
                              Number of missing is {info.nb_missing_in} :
                              {hidden2==true?info.missing.map((m, index) => {
                                return <p>{JSON.stringify(m)}</p>;
                                // else return " - " + JSON.stringify(m);
                              }):""}
                            </p>
                          </p>
                        ) : (
                          <p className="bg-ligh text-black p-3 rounded" style={{color:"white"}}>
                            {/* <span>
                              <i
                                style={{ fontSize: "35px" }}
                                class="bi bi-check-lg"
                              ></i>
                            </span> */}
                            <p>Number of missing is : {info.nb_missing_in}</p>
                          </p>
                        )
                      ) : (
                        ""
                      )}
                      </div>
                    </div>
                    <div className="col-2">
                      {percentage == 100 ? (
                        info.nb_missing_in != 0 ? (
                          <p className="bg-danger p-3 rounded" style={{color:"white"}}>
                            <span >
                              <i
                                style={{ fontSize: "35px" }}
                                class="bi bi-x-lg"
                              ></i>
                            </span>
                            <p>
                              Number of missing is {info.nb_missing_in} :
                              {/* {info.missing.map((m, index) => {
                                if (index == 0) return " " + JSON.stringify(m);
                                else return " - " + JSON.stringify(m);
                              })} */}
                            </p>
                          </p>
                        ) : (
                          <p className="bg-success p-3 rounded" style={{color:"white"}}>
                            <span>
                              <i
                                style={{ fontSize: "35px" }}
                                class="bi bi-check-lg"
                              ></i>
                            </span>
                            <p>Number of missing is : {info.nb_missing_in}</p>
                          </p>
                        )
                      ) : (
                        "NO DATA AVAILABLE"
                      )}
                    </div>
                  </div>
                </Card>
              </div>
            );
          })}
          </TabPanel>
        })}
      </Tabs>
      </div>
    </div>
  );
}

export default App;
